import Typography from 'typography';

const typography = new Typography({
  baseFontSize: '18px',
  baseLintHeight: 1.666,
  bodyWeight: 200,
  headerWeight: 100,
  headerFontFamily: ['Covered By Your Grace', 'cursive'],
  bodyFontFamily: ['Roboto', 'sans-serif'],
});

export default typography;



